.project_datails {
    padding: 2em;
}

.project_datails h1,
.project_datails h2,
.project_datails p {
    margin-bottom: 0.5em;
}

.project_datails h1 {
    background-color: #222;
    color: #ffbb33;
    padding: 0.4em;
}
.project_datails span{
    font-weight: bold;
}

.details_container,
.service_form_container{
    border-bottom: 1px solid #7a7a7a;
    margin-bottom: 1.2em;
    padding-bottom: 1.2em;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 100%;
}
.btn{
    background-color: #222;
    color: #fff;
    padding: 0.5em 1em;
    text-decoration: none;
    transition: 0.5s;
    cursor: pointer;
    max-height: 40px;
    border: none;
}

.btn:hover{
    color: #ffbb33;
}

.project_info {
    width: 100%;
}